@use "sass:list";

:host {
  display: block;
}

:host .group-item {
  > th {
    cursor: auto;

    > div {
      line-height: 1;
      margin: 0.25rem;
    }
  }
}

:host span.detail-title {
  display: inline-block;
  min-width: 75px;
  font-weight: bold;
}

:host .d-table-none {
  display: none !important;
}

:host .d-table-cell {
  display: table-cell !important;
}

// 響應式類別設定
$sizes: 4xs, 3xs, xxs, xs, sm, md, lg, xl, xxl, 3xl, 4xl;

@each $innerSize in $sizes {
  $none-selectors: ();
  $cell-selectors: ();

  @each $size in $sizes {
    @if list.index($sizes, $size) >= list.index($sizes, $innerSize) {
      $none-selectors: list.append($none-selectors, ":host .storm-table-#{$size} .d-table-#{$innerSize}-none", comma);
    }

    @if list.index($sizes, $size) >= list.index($sizes, $innerSize) {
      $cell-selectors: list.append($cell-selectors, ":host .storm-table-#{$size} .d-table-#{$innerSize}-cell", comma);
    }
  }

  #{$none-selectors} {
    display: none !important;
  }

  #{$cell-selectors} {
    display: table-cell !important;
  }
}
