import { Component, Element, Event, EventEmitter, forceUpdate, h, Host, Listen, Prop, State } from '@stencil/core';
import { isCommandString, parseCommandString } from '../../../utils/command-utils/command-utils';
import { invokeDotNetMethodAsync } from '../../../utils/dotnet-method-invoker/dotnet-method-invoker';
import { FnIconSwitch } from '../../../utils/functional/functional';
import {
  appendHighlight,
  escapeHtml,
  evaluateConditions,
  getFieldValue,
  isConditionGroups,
  isConditions,
  replacePlaceholders,
  resolvePropertyValue,
  setFieldValue,
} from '../../../utils/utils';
import type { ICheckboxItem } from '../../storm-checkbox-group/storm-checkbox-group';
import type { CheckboxGroupInterface, TableColumnInterface } from '../interface';
import { StormTable } from '../storm-table';
import type { ClickActionDetail } from '../storm-table-toolbar/interface';
import type {
  CheckboxGroupControlOptionInterface,
  CheckControlOptionInterface,
  DropdownControlOptionInterface,
  DropdownTreeControlOptionInterface,
  TextAreaControlOptionInterface,
} from './interface';

@Component({
  tag: 'storm-table-cell',
  styleUrl: 'storm-table-cell.css',
  shadow: false,
})
export class StormTableCell {
  @State() private cellData: any;
  @State() private cellType: string = 'text';
  @State() private isDisabled: boolean = false;
  @State() private prependText?: string;
  @State() private appendText?: string;

  @Element() host: HTMLElement;

  @Prop() table: StormTable;
  @Prop() row: object;
  @Prop() index: number;
  @Prop() column: TableColumnInterface;
  @Prop({ mutable: true }) timeStamp?: number;

  @Event() clickCell: EventEmitter<{ id: string; field: string | number }>;
  @Event() clickAction: EventEmitter<ClickActionDetail>;
  @Event() valueChanged: EventEmitter<any>;
  @Event() cellValueChanged: EventEmitter<{ id: string; field: string | number; value: any }>;

  @Listen('valueChanged')
  valueChangedHandler(ev: CustomEvent<any>) {
    if (this.column.field) {
      setFieldValue(this.row, this.column.field, ev.detail);
      this.cellValueChanged.emit({ id: getFieldValue(this.row, this.table.fieldId) as string, field: this.column.field, value: ev.detail });
    }
  }

  @Listen('cellValueChanged')
  cellValueChangedHandler(_: CustomEvent<{ id: string; field: string | number; value: any }>) {
    forceUpdate(this.host);
  }

  renderSerialNumber() {
    return (this.table.pageNumber - 1) * this.table.pageSize + this.index + 1;
  }

  renderDetail() {
    return (
      <FnIconSwitch
        onToggle={on => {
          const detailRow = this.table.host.shadowRoot.querySelector('.row-detail[data-index="' + this.index + '"]');
          if (detailRow) {
            detailRow.classList.toggle('d-none', !on);
          }
        }}
      />
    );
  }

  renderActionControl() {
    return (
      <storm-table-toolbar
        table={this.table}
        column={this.column}
        row={this.row}
        timeStamp={this.timeStamp}
        onClickAction={ev => {
          ev.stopPropagation();
          this.clickAction.emit(ev.detail);
        }}
      ></storm-table-toolbar>
    );
  }

  renderInputControl() {
    return (
      <div class="input-group input-group-static">
        <input
          class="form-control"
          type="text"
          aria-label={this.column.title}
          disabled={this.isDisabled}
          value={getFieldValue(this.row, this.column.field) as string | number | string[]}
          onClick={ev => ev.stopPropagation()}
          onChange={ev => {
            ev.stopPropagation();
            this.valueChanged.emit((ev.target as HTMLInputElement).value);
          }}
        />
      </div>
    );
  }

  renderTextareaControl() {
    const option = this.cellData as TextAreaControlOptionInterface;
    return (
      <div class="input-group input-group-static">
        <textarea
          class="form-control"
          disabled={this.isDisabled}
          cols={option.cols}
          rows={option.rows}
          title={this.column.title}
          value={getFieldValue(this.row, this.column.field) as string}
          onClick={ev => ev.stopPropagation()}
          onChange={ev => {
            ev.stopPropagation();
            this.valueChanged.emit((ev.target as HTMLInputElement).value);
          }}
        />
      </div>
    );
  }

  renderDropdownControl() {
    const option = this.cellData as DropdownControlOptionInterface;
    const dataSet = option.dataSet.map(item => ({
      ...item,
    }));
    return (
      <storm-dropdown
        disabled={this.isDisabled}
        label={option.label}
        value={getFieldValue(this.row, this.column.field) as string | string[]}
        dataSet={dataSet}
        nullable={option.nullable ?? false}
        searchable={option.searchable ?? true}
        selection={'single'}
        timeStamp={this.timeStamp}
        type={'select'}
        onValueChanged={ev => {
          ev.stopPropagation();
          this.valueChanged.emit(ev.detail);
        }}
      ></storm-dropdown>
    );
  }

  renderDropdownTreeControl() {
    const option = this.cellData as DropdownTreeControlOptionInterface;
    let dataSet = option.dataSet;

    if (option.dataSets && isConditionGroups(option.dataSets)) {
      const matchingConditionGroup = option.dataSets.find(conditionGroup => isConditions(conditionGroup.conditions) && evaluateConditions(conditionGroup.conditions, this.row));
      if (matchingConditionGroup) {
        dataSet = matchingConditionGroup.value;
      }
    }
    return (
      <storm-dropdown-tree
        disabled={this.isDisabled}
        loading={option.loading}
        partialLoad={option.partialLoad}
        label={option.label}
        value={getFieldValue(this.row, this.column.field) as string | string[]}
        text={option.fieldText ? (getFieldValue(this.row, option.fieldText) as string) : null}
        dataSet={dataSet}
        dataSource={option.dataSource}
        fieldId={option.fieldId ?? 'id'}
        fieldLabel={option.fieldLabel ?? 'label'}
        fieldChildren={option.fieldChildren ?? 'children'}
        expandLevel={option.expandLevel ?? 2}
        nodeSelectableRule={option.nodeSelectableRule}
        nullable={option.nullable ?? false}
        searchable={option.searchable ?? true}
        selection={'single'}
        timeStamp={this.timeStamp}
        onValueChanged={ev => {
          ev.stopPropagation();
          this.valueChanged.emit(ev.detail);
        }}
        onTextChanged={ev => {
          ev.stopPropagation();
          if (option.fieldText) setFieldValue(this.row, option.fieldText, ev.detail);
        }}
        onSearchChanged={ev => {
          ev.stopPropagation();
        }}
      ></storm-dropdown-tree>
    );
  }

  renderCheckControl() {
    return (
      <storm-checkbox
        type={this.cellType === 'checkbox' ? 'checkbox' : 'radio'}
        disabled={this.isDisabled}
        checked={getFieldValue(this.row, this.column.field) as boolean}
      ></storm-checkbox>
    );
  }

  renderCheckboxGroupControl() {
    const option = this.cellData as CheckboxGroupControlOptionInterface;
    const value = getFieldValue(this.row, this.column.field) as any;
    const dataSet = (option.dataSet as CheckboxGroupInterface[])
      .filter(x => x.visible === undefined || (typeof x.visible === 'function' && x.visible(this.row)) || (typeof x.visible === 'boolean' && x.visible))
      .map(
        x =>
          ({
            id: x.id,
            label: x.label,
            labelClasses: x.labelClasses,
            checked: x.checked,
            disabled: x.disabled,
          } as ICheckboxItem),
      );
    return (
      <div class="input-group input-group-static">
        <storm-checkbox-group
          disabled={this.isDisabled}
          type={option.groupType}
          value={value}
          dataSet={dataSet}
          isSwitch={option.isSwitch ?? false}
          isInline={option.isInline ?? true}
          onValueChanged={ev => {
            ev.stopPropagation();
            this.valueChanged.emit(ev.detail);
          }}
        ></storm-checkbox-group>
      </div>
    );
  }

  renderCellContent() {
    switch (this.cellType) {
      case 'action':
        return this.renderActionControl();
      case 'input':
        return this.renderInputControl();
      case 'checkbox':
      case 'radio':
        return this.renderCheckControl();
      case 'textarea':
        return this.renderTextareaControl();
      case 'dropdown':
        return this.renderDropdownControl();
      case 'dropdownTree':
        return this.renderDropdownTreeControl();
      case 'checkboxGroup':
        return this.renderCheckboxGroupControl();
      case 'serialNumber':
        return this.renderSerialNumber();
      case 'detail':
        return this.renderDetail();
      case 'text':
        return this.renderCellValue();
      default:
        return null;
    }
  }

  renderCell() {
    if (!this.prependText && !this.appendText) {
      return this.renderCellContent();
    } else {
      return (
        <div class="d-flex flex-row gap-1 align-items-center">
          {this.prependText && <span>{this.prependText}</span>}
          {this.renderCellContent()}
          {this.appendText && <span>{this.appendText}</span>}
        </div>
      );
    }
  }

  renderCellValue() {
    let content = getFieldValue(this.row, this.column.field) as any;

    if (typeof this.column.render === 'function') {
      content = this.column.render(content, this.row, this.index);
    } else {
      if (Array.isArray(content)) {
        content = content.filter(item => item != null && item !== '').join('、');
      }

      if (this.table.escape && typeof content !== 'boolean' && content != null) {
        content = escapeHtml(content);
      }

      if (content != null && this.column.transform && this.column.transform[content.toString()]) {
        content = this.column.transform[content];
      } else if (content && this.table.searchable && this.table.searchText) {
        content = appendHighlight(content, this.table.searchText);
      }
    }

    const template = this.column.cellTemplate || this.table.cellTemplate;
    const displayValue = content != null && content.toString().trim().length > 0 ? content : '-';
    return (
      <span
        innerHTML={replacePlaceholders(template, {
          value: displayValue,
          row: this.row,
          index: this.index,
        })}
      ></span>
    );
  }

  async componentWillRender() {
    if (!this.column) {
      this.cellType = 'text';
      this.cellData = undefined;
      this.isDisabled = false;
      this.prependText = undefined;
      this.appendText = undefined;
      return;
    }

    const [cellType, cellData] = await Promise.all([this.resolvePropertyValueAsync<string>(this.column, 'type'), this.resolvePropertyValueAsync(this.column, 'data')]);

    this.cellType = cellType || 'text';
    this.cellData = cellData;

    if (cellData && typeof cellData === 'object' && !Array.isArray(cellData)) {
      const [disabled, prependText, appendText] = await Promise.all([
        this.resolvePropertyValueAsync<boolean>(cellData, 'disabled'),
        this.resolvePropertyValueAsync<string>(cellData, 'prependText'),
        this.resolvePropertyValueAsync<string>(cellData, 'appendText'),
      ]);

      this.isDisabled = disabled ?? false;
      this.prependText = prependText;
      this.appendText = appendText;

      if (this.isDisabled && (cellType === 'input' || cellType === 'textarea')) {
        this.cellType = 'text';
      }
    } else {
      this.isDisabled = false;
      this.prependText = undefined;
      this.appendText = undefined;
    }
  }

  private async resolvePropertyValueAsync<T>(targetObject: any, propertyName: string): Promise<T> {
    const result = resolvePropertyValue(targetObject, propertyName, [this.row, this.index, this.column]);
    if (isCommandString(result)) {
      const parsed = parseCommandString(result);
      if (parsed.command === 'Invoke:DotNet') {
        const rowId = getFieldValue(this.row, this.table?.fieldId) as string;
        try {
          return await invokeDotNetMethodAsync(parsed.value, [rowId, this.index, this.column.field], { cache: true, regionName: this.table.uid });
        } catch (error) {
          console.error('InvokeDotNetMethod 執行失敗', error);
          throw error;
        }
      }
      if (parsed.command === 'Get:Field') {
        return getFieldValue(this.row, parsed.value) as T;
      }
    }

    if (isConditions(result)) {
      return evaluateConditions(result, this.row) as T;
    }

    if (isConditionGroups(result)) {
      for (const conditionGroup of result) {
        if (isConditions(conditionGroup.conditions) && evaluateConditions(conditionGroup.conditions, this.row)) {
          return conditionGroup.value as T;
        }
      }
      return undefined;
    }

    if (result instanceof Promise) {
      try {
        const resolved = await result;
        return resolved;
      } catch (error) {
        console.error('屬性 Promise 解析失敗', error);
        throw error;
      }
    }

    return result;
  }

  render() {
    return (
      <Host
        onClick={ev => {
          if (this.cellType === 'checkbox' || this.cellType === 'radio') {
            const option = this.cellData as CheckControlOptionInterface;
            if (option && option.disabled) {
              ev.stopPropagation();
              ev.preventDefault();
              return;
            }
          }
          this.clickCell.emit({ id: getFieldValue(this.row, this.table.fieldId) as string, field: this.column.field });
        }}
      >
        {this.renderCell()}
      </Host>
    );
  }
}
