import type { ParsedCommand } from './interface';

/**
 * 判斷輸入是否為有效命令格式，例如 @Invoke:DotNet('GetUser')
 * @param value 任意輸入
 * @returns 是否符合命令格式
 */
export function isCommandString(value: unknown): value is string {
    if (typeof value !== 'string') return false;
    // 檢查型別與正則，確保格式正確
    // 僅允許單層括號內容，且括號內可為任意字元
    return (
        typeof value === 'string' &&
        /^@[a-zA-Z0-9:-]+\('.*'\)$/.test(value.trim())
    );
}

/**
 * 解析命令字串，回傳 ParsedCommand 或 null
 * 格式範例：@Invoke:DotNet('GetUser')
 * @param value 命令字串
 * @returns 解析後的命令物件，若格式不符則回傳 null
 */
export function parseCommandString(value: string): ParsedCommand | null {
    const trimmed = value.trim();
    // 強化正則表達式，避免括號內為空或出現多行情況
    const commandRegex = /^@([a-zA-Z0-9:-]+)\('([^']*)'\)$/;

    const match = commandRegex.exec(trimmed);
    if (!match) return null;

    return {
        command: match[1],
        value: match[2]
    };
}

/**
 * 檢查解析後命令是否符合指定 path
 * @param parsed 解析後的命令物件
 * @param expectedPath 預期的 path 字串，例如 "Invoke:DotNet"
 * @returns 是否符合預期 path
 */
export function isPath(parsed: ParsedCommand | null, expectedPath: string): boolean {
    return !!parsed && parsed.command === expectedPath;
}