import { Component, Element, Event, EventEmitter, Host, Method, Prop, forceUpdate, h } from '@stencil/core';
import { getFieldValue, parseClassNames } from '../../../utils/utils';
import type { TableColumnInterface } from '../interface';
import { StormTable } from '../storm-table';

@Component({
  tag: 'storm-table-body',
  styleUrl: 'storm-table-body.scss',
  shadow: false,
})
export class StormTableBody {
  @Element() host: HTMLElement;

  @Prop() table: StormTable;
  @Prop() dataSet: object[];
  @Prop({ mutable: true }) timeStamp?: number;

  @Event() clickRow: EventEmitter<string>;

  @Method()
  async updateColumnCheckbox(col: TableColumnInterface) {
    if (!this.dataSet || col.type !== 'checkbox') return;
    const checkedCount = this.getTotalChecked(col.field);
    const stormCheckbox = this.host.querySelector('th[data-field="' + col.field + '"] storm-checkbox') as HTMLStormCheckboxElement;

    stormCheckbox.checked = checkedCount === this.table.totalRows;
    stormCheckbox.indeterminate = checkedCount > 0 && checkedCount < this.table.totalRows;
  }

  @Method()
  async updateRowCheckbox(row: object, col: TableColumnInterface) {
    const cell = this.host.querySelector(
      'tr[data-id="' + getFieldValue(row, this.table.fieldId) + '"] storm-table-cell[data-field="' + col.field + '"]',
    ) as HTMLStormTableCellElement;
    if (cell) {
      forceUpdate(cell);
    }
    if (col.field === this.table.fieldSelected) {
      await this.updateRowSelection(row);
    }
  }

  @Method()
  async updateRowSelection(row: object) {
    const el = this.host.querySelector('tr[data-id="' + getFieldValue(row, this.table.fieldId) + '"]') as HTMLInputElement;
    if (el) el.classList.toggle('table-primary', getFieldValue(row, this.table.fieldSelected) as boolean);
  }

  private computeColumnClasses(col: TableColumnInterface, classes?: Record<string, boolean>): Record<string, boolean> {
    return {
      'text-center': col.align === 'center',
      'text-start': !col.align || col.align === 'left',
      'text-end': col.align === 'right',
      'align-middle': !col.valign || col.valign === 'middle',
      'align-top': col.valign === 'top',
      'align-bottom': col.valign === 'bottom',
      'font-weight-bolder': true,
      'text-truncate': true,
      'table-sorter': col.sortable,
      asc: this.table.sortName === col.field && this.table.sortOrder !== 'desc',
      desc: this.table.sortName === col.field && this.table.sortOrder === 'desc',
      ...(classes || {}),
      ...(col.classes || {}),
    };
  }

  renderThead() {
    if (!this.table.columns) return;
    return (
      <thead>
        <tr>
          {this.table.columns
            .filter(col => col.visible !== false)
            .map(col => {
              return this.renderColumn(col);
            })}
        </tr>
      </thead>
    );
  }

  renderColumn(col: TableColumnInterface) {
    let content: any = col.title;
    const classes: any = {
      'thead-xxs': col.type === 'detail' || col.type === 'serialNumber' || col.type === 'radio' || col.type === 'checkbox',
      'detail-toggle': col.type === 'detail',
    };

    if (content == null && col.type === 'checkbox') {
      const checkedCount = this.getTotalChecked(col.field);
      content = (
        <storm-checkbox
          type="checkbox"
          indeterminate={checkedCount > 0 && checkedCount < this.table.totalRows}
          checked={checkedCount === this.table.totalRows}
          onValueChanged={ev => this.handleColumnCheckChange(ev, col)}
        ></storm-checkbox>
      );
    }

    return this.renderHeaderCell(content, col, classes);
  }

  renderHeaderCell(content: any, col: TableColumnInterface, classes: Record<string, boolean> = {}) {
    return (
      <th data-field={col.field} scope="col" class={this.computeColumnClasses(col, classes)} onClick={() => this.handleColumnClick(col)}>
        <div class="cell">{content}</div>
        {col.description && <div class="table-description">{col.description}</div>}
      </th>
    );
  }

  handleColumnClick(col: TableColumnInterface) {
    if (!col.sortable) return;
    if (col.field == this.table.sortName) {
      if (this.table.sortOrder == 'desc') this.table.sortChange.emit({ sortName: undefined, sortOrder: undefined });
      else this.table.sortChange.emit({ sortName: this.table.sortName, sortOrder: 'desc' });
    } else this.table.sortChange.emit({ sortName: col.field, sortOrder: 'asc' });
  }

  async handleColumnCheckChange(ev: CustomEvent<boolean>, col: TableColumnInterface) {
    const checked = ev.detail;
    if (checked) await this.table.checkAll(col.field);
    else await this.table.uncheckAll(col.field);
  }

  async initSelect() {
    if (!this.dataSet || !this.table.columns) return;

    for (const row of this.dataSet) {
      await this.updateRowSelection(row);
    }
  }

  getTotalChecked(field: string | number): number {
    if (!this.table.totalChecked) return 0;
    return this.table.totalChecked[field];
  }

  private computeCellClasses(col: TableColumnInterface, classes?: Record<string, boolean>): Record<string, boolean> {
    return {
      'text-center': col.align === 'center',
      'text-start': !col.align || col.align === 'left',
      'text-end': col.align === 'right',
      'align-middle': !col.valign || col.valign === 'middle',
      'align-top': col.valign === 'top',
      'align-bottom': col.valign === 'bottom',
      ...(classes || {}),
      ...(col.classes || {}),
    };
  }

  renderCell(row: object, index: number, col: TableColumnInterface) {
    const cellClasses = this.computeCellClasses(col, { 'text-xs': col.type === 'serialNumber', 'detail-toggle': col.type === 'detail' });
    return (
      <td data-field={col.field} class={cellClasses}>
        {this.renderCellContent(row, index, col)}
      </td>
    );
  }

  renderCellContent(row: object, index: number, col: TableColumnInterface) {
    return <storm-table-cell table={this.table} row={row} index={index} column={col} time-stamp={this.timeStamp} data-field={col.field}></storm-table-cell>;
  }

  private renderRow(row: object, index: number) {
    const rowId = getFieldValue(row, this.table.fieldId) as string;
    const isSelected = getFieldValue(row, this.table.fieldSelected) as boolean;
    const visibleColumns = this.table.columns.filter(col => col.visible !== false);

    const mainRow = (
      <tr data-id={rowId} class={{ 'table-primary': isSelected }} onClick={() => this.clickRow.emit(rowId)}>
        {visibleColumns.map(col => this.renderCell(row, index, col))}
      </tr>
    );

    if (this.table.isColumnResponsive) {
      const detailCol = this.table.columns.find(col => col.type === 'detail');
      const detailClasses = detailCol?.classes ? { ...detailCol.classes, 'row-detail': true, 'd-none': true } : { 'row-detail': true, 'd-none': true };

      return [
        mainRow,
        <tr class={detailClasses} data-index={index}>
          <td class="full-cell" colSpan={this.table.expand.visibleColumnCount}>
            <ul class="list-group list-group-flush">{this.renderDetail(row, index)}</ul>
          </td>
        </tr>,
      ];
    }

    return mainRow;
  }

  renderDetail(row: object, index: number) {
    return this.table.columns
      .filter(col => col.responsive === true && col.visible !== false)
      .map(col => (
        <li class={{ 'list-group-item': true, ...(col.rwdClassReversal || {}) }}>
          <span class="detail-title">{col.title}</span>
          <span>{this.renderCellContent(row, index, col)}</span>
        </li>
      ));
  }

  private renderRows() {
    if (this.table.groupBy) {
      let groupItem: string = '';
      const result: any[] = [];

      this.dataSet.forEach((row, i) => {
        const groupByValue = getFieldValue(row, this.table.groupBy) as string;

        if (groupByValue !== groupItem) {
          groupItem = groupByValue;
          result.push(
            <tr class="group-item table-info">
              <th class="align-middle full-cell" colSpan={this.table.expand.visibleColumnCount}>
                {groupByValue}
              </th>
            </tr>,
          );
        }
        result.push(this.renderRow(row, i));
      });
      return result;
    }

    return this.dataSet.map((row, index) => this.renderRow(row, index));
  }

  private renderBody() {
    if (!this.dataSet?.length) {
      const emptyClasses = {
        'align-middle': true,
        'text-center': true,
        'full-cell': true,
        ...(this.table.classes?.['table-sm'] ? { 'height-100': true } : { 'height-300': true }),
      };

      return (
        <tbody>
          <tr>
            <td class={emptyClasses} colSpan={this.table.expand.visibleColumnCount}>
              <p class="p-4 h6">沒有找到符合的結果</p>
            </td>
          </tr>
        </tbody>
      );
    }
    return <tbody>{this.renderRows()}</tbody>;
  }

  render() {
    return (
      <Host>
        <table class={{ ...{ table: true, 'mb-0': true, 'table-hover': !!this.table.clickRowTo }, ...parseClassNames(this.table.classes) }}>
          {this.renderThead()}
          {this.renderBody()}
        </table>
      </Host>
    );
  }

  async componentDidRender() {
    await this.initSelect();
  }
}
